*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
nav{
    height:60px;
    display: flex;
    justify-content: space-between;
    padding:10px 30px;
    align-items: center;
    background-color: #FFCC70;
}
nav ul{
    display: flex;
    margin-right: 10px;
    align-items: center;
}
nav ul li{
    list-style: none;
    margin-right: 10px;

}
nav ul li:hover{
    color: orange;
}
#logo{
    height: 40px;
}
#switchBtn{
  height: 30px;    
}