.imagesDiv{
   position: absolute;
   right: 100px;
   bottom: 60px;
   width: 45%;
   height: 80%;
}
.imagesDiv img{
    height: 600px;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    transition: bottom 1s,left 1s;
}

.imagesDiv:hover #background{
    bottom: 40px;
}
.imagesDiv:hover #girl{
    left: 54%;
}
@media screen and (max-width: 450px) {
    .imagesDiv img{
        height: 420px;
    }
}@media screen and (max-width: 380px) {
    .imagesDiv img{
        height: 220px;
    }
}
@media screen and (max-width: 1370px) {
    .imagesDiv img{
        height: 500px;
    }
}